<template>
    <div id='gymbranch' class='leakind'>
    	<div class="selsectSeat">
        <div class="top">
          <span class="title">坐次表</span>
          <div class="role">
            <span class="coach" :class="{active: roleFlag == 'coach'}" @click="roleClick('coach')">教练选坐</span>
            <span class="student" :class="{active: roleFlag == 'student'}" @click="roleClick('student')">学员选坐</span>
          </div>
        </div>
        <div class="seatTable">
          <div class="addRow">
            <img src='../../assets/images/addvipkind/btn_add.png' @click="changeChiar('addClo')" class="btnadd">
            <img src='../../assets/images/addvipkind/btn_less.png' @click="changeChiar('reduceClo')" class="btnless">
          </div>
          <div class="addClo">
            <img src='../../assets/images/addvipkind/btn_add.png' @click="changeChiar('addRow')" class="btnadd">
            <img src='../../assets/images/addvipkind/btn_less.png' @click="changeChiar('reduceRow')" class="btnless">
          </div>
          <ul class="chair" v-for="(item, i) in seatArr" :key="i">
              <li class="selSeat" v-for="(cell, j) in item" :key="j" @click="seatClick(cell,i,j,roleFlag)" :class="{'aa': cell.status == 0,'bb': cell.status == 1,'cc': cell.status == 2,'dd': cell.status == 3}">
                <!-- {{cell.name}} -->
              </li>
          </ul>
        </div>
      </div>
    </div>
</template>
<script>
import {updategroupcourse, getcourselabels, upload, addgroupcourse, vipCardKindList} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../../utils/com.js'
import { setTimeout } from 'timers';
import Vue from 'vue';
export default {
  data () {
    return {
      
    }
  },
  mounted () {
    for(var i=0;i<this.rows;i++){//一维数组长度为30  
      this.seatArr[i]=new Array();  
      for(var j=0;j<this.clow;j++){//第二维长度为20  
        this.seatArr[i][j] = {
          name: 'qq',
          status: 1
        }
      }
    }
  },
  methods: {
    changeSelect () {
    },
    seatClick (cell,i,j,roleFlag) {
      
    },
    // 返回到私教课程页面
    goback () {
      this.$router.push({name: 'leaguekind'})
    }
  }
}
</script>
<style lang="sass" scoped>
#gymbranch
    .el-form
      height: 6.3rem;
      .vip
        padding-left: 0.2rem;
        align-items: center;
      .vip.nvip
        padding-left: 0.1rem;
    .btn
        display: flex;
        justify-content: center;
        display: inline-block;
        text-align: center;
        .submit
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .14rem;
            width: 1.2rem;
            height: .44rem;
            line-height: .44rem;
            color: #fff;
            text-align: center;
            margin-top: .3rem;
            cursor: pointer;
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
        .branch
            font-size: .21rem;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;
</style>
<style <style lang="scss" scoped>
.leakind .el-form .info .right2, .leakind .el-form .info .center{
  margin-left: .51rem!important;
}
.leakind .el-form{
  padding-right: 0.5rem;
  .payMethod{
    .el-form-item__content{
      .el-checkbox-group{
        label{
          display: block;
        }
      }
      .childCheck{
        margin-left: .2rem;
        label{
          display: inline-block;
        }
      }
    }
  }
  .pricetips{
      font-family: PingFangSC-Medium;
      font-size: 14px;
      color: #C0C4CC;
      letter-spacing: 0;
      line-height: 14px;
      text-align: right;
      margin-top: -14px;
      margin-bottom: 20px;
  }
  .newPay{
    flex-flow: column;
    .el-form-item__content{
      .el-checkbox{
          padding-left: .2rem;
      }
      .el-checkbox-group > div{
        margin-bottom: .12rem;
        background: #FFFFFF;
        border: 1px solid #EAECEF;
        position: relative;
      }
      .childCheck{
        margin-left: 0;
        border-top: 1px solid #EAECEF;
        .is-checked{
          width: auto !important;
          background:#fff !important;
        }
        label{
          // width: 100%;
        }
      }
      .itemList{
        .is-checked{
          width: 100%;
          background: rgba(233,237,239,.2);
        }
      }
    }
    .nocard{
      z-index: 1;
      font-size: .14rem;
      font-family: PingFangSC-Medium;
      color: #8597A1;
      letter-spacing: 0;
      //margin-right: .2rem;
      position: absolute;
      top: 0px;
      right: .2rem;
    }
    .addCard{
      height: .76rem;
      z-index: 1;
      color: #20c6ba;
      font-size: 14px;
      border-top: 1px solid #EAECEF;
      /* line-height: .76rem; */
      padding: .2rem;
      div{
        background: rgba(32,198,186,0.05);
        border-radius: 2px;
        width: 1rem;
        height: .36rem;
        line-height: .36rem;
        text-align: center;
        cursor: pointer;
      }
      span{
        margin-left: .05rem;
      }
    }
  }
}
.marginleft.formNew .el-form-item__label:after {
  margin-left: 0.07rem;
}
.selsectSeat{
  position: relative;
}
.selsectSeat ul, .selsectSeat li{
  list-style: none;
}
.selsectSeat .top{
  width: 100%;
  height: 0.3rem;
  justify-content: space-between;
  align-items: center;
}
.top .title{
  font-size: 0.16rem;
  color: #8597A1;
}
.top  .role{
  display: flex;
}
.top  .role .coach{
  margin-right: 0.1rem;
}
.coach.active{
  background: #FC7563;
  border: 0;
  color: #fff!important;
}
.student.active{
  background: #20C6BA;
  border: 0;
  color: #fff!important;
}
.top  .role .student, .top  .role .coach{
  border: 1px solid #E9ECEF;
  border-radius: 4px;
  font-size: 0.14rem;
  color: #909399;
  width: 0.8rem;
  height: 0.28rem;
  line-height: 0.28rem;
  text-align: center;
  cursor: pointer;
}
.seatTable{
  position: relative;
  padding-top: 0.2rem;
  padding-left: 0.2rem; 
  width: min-content;
}
.seatTable .addRow{
  height: 0.2rem;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(237,239,241,0.23);
}
.btnadd, .btnless{
  position: absolute;
}
.seatTable .addRow .btnadd{
  right: 0.1rem;
  top: 0.04rem;
}
.seatTable .addRow .btnless{
  right: 0.3rem;
  top: 0.04rem;
}
.seatTable .addClo .btnadd{
  bottom: 0.1rem;
  left: 0.04rem;
}
.seatTable .addClo .btnless{
  bottom: 0.3rem;
  left: 0.04rem;
}
.seatTable .addRow img, .seatTable .addClo img{
  width: 0.12rem;
  height: 0.12rem;
}
.seatTable .addClo{
  width: 0.2rem;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(237,239,241,0.23);
}
.chair{
  display: flex;
  padding: 0;
  margin: 0;
}
.selSeat{
  background: #EDEFF1;
  width: 0.3rem;
  height: 0.3rem;
  margin-top: 2px;
  margin-left: 2px;
}
.aa {
  background: black;
}
.bb{
  /* background: gray; */
}
.cc{
  background: #FC7563;
}
.dd{
  background: #20C6BA;
}
.leakind .el-form .payMethod .el-form-item__content .el-checkbox {
  margin-left:0;
  margin-right: 0.3rem;
}
</style>



